import { Input } from '@/view/component/input'
import { notify } from '@/view/component/notify'
import type { ShowCaseOptions } from '@/view/component/showCase'
import { ShowCase } from '@/view/component/showCase'
import cssComponents from 'css-components'
import cls from './main.module.scss'

export const Main = () => {
  const searchChange = (value: string) => {
    notify('success', value)
  }

  const component = cssComponents[0]

  const options: ShowCaseOptions = {
    id: 'id',
    title: 'title',
    desc: 'desc',
    category: 'category',
    tags: ['tag1', 'tag2'],
    html: component.html,
    css: component.css,
    js: undefined,
    code: [
      {
        label: 'html',
        filetype: 'html',
        content: component.html
      },
      {
        label: 'css',
        filetype: 'css',
        content: component.css
      }
    ]
  }

  return (
    <div className={cls.main}>
      <Input onChange={searchChange} placeholder="Search Ideas" />
      <ShowCase options={options} />
    </div>
  )
}
